	module.exports = {"data":{"markdownRemark":{"html":"<h2>Stochastic Process</h2>\n<p>A Stochastic Process is basically a process which is non-deterministic. Note that it may have a probability distribution and may not be completely random. For instance the process where a 2-sided <em>unfair</em> coin is flipped again and again. Or for instance, direction a drop of water may take on a roughly plain surface (non-determinism here may arise due to occassional roughness which makes it hard to determine the path of the droplet).</p>\n<p>Stochastic processes are widely used as mathematical models of systems and phenomena that appear to vary in a random manner. They have applications in many disciplines including the natural sciences such as biology, chemistry and physics as well as technology and engineering fields such as image processing, signal processing, information theory, computer science, cryptography and telecommunications. Furthermore, seemingly random changes in financial markets have motivated the extensive use of stochastic processes in finance.</p>\n<h3>More Information:</h3>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Stochastic_process\">Stochastic Process</a></li>\n</ul>","fields":{"slug":"/machine-learning/stochastic-process/"},"frontmatter":{"title":"Stochastic Process"}}},"pathContext":{"slug":"/machine-learning/stochastic-process/"}}

